*{
    padding:0;
    margin:0;
    box-sizing: border-box;
}
body,html{
    padding:0;
    margin:0;
    background-color: antiquewhite;
}

.split {
    height: 100%;
    width: 50%;
    position: fixed;
    z-index: 1;
    top: 0;
    overflow-x: hidden;
    /* padding-top: 20px; */
    margin-top:50px;
    /* margin-right: 20px;
    margin-left:20px; */
    
  }
  
  /* Control the left side */
  .left {
    left: 0;
    
    background-color: rgb(179, 170, 170);

  }
  
  /* Control the right side */
  .right {
    right: 0;
    background-color: rgba(255, 0, 0, 0.349);
  }
  .centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
label {
  padding: 15px 12px 16px 0;
  display: inline-block;
  margin-right:30px;
}
input[type=text], input[type=password] {
  width: 100%;
  
  padding: 12px;
  margin: 15px 15px 15px 15px;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
  resize: vertical;
  background: #f1f1f1;
}

input[type=text]:focus, input[type=password]:focus {
  background-color: #ddd;
  outline: none;
}
button {
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  opacity: 0.9;
  
}
.container {
  border-radius: 2px;
  background-color: #f2f2f2;
  padding: 20px;
}
.col-25 {
  float: left;
  width: 25%;
  margin-top: 6px;
}

.col-75 {
  float: left;
  width: 75%;
  margin-top: 6px;
}
.row:after {
  content: "";
  display: table;
  clear: both;
}

